import { IsOptional, IsString, IsNumber, IsIn, ValidateIf, IsNotEmpty, Min } from 'class-validator';

export class UpdateDeviceConfigDto {
  @ValidateIf((o) => o.status === 'active')
  @IsNotEmpty({ message: 'Bank Code is required when status is active' })
  @IsString()
  @IsOptional()
  bankCode?: string;

  @ValidateIf((o) => o.status === 'active')
  @IsNotEmpty({ message: 'Available Amount is required when status is active' })
  @IsNumber()
  @Min(0, { message: 'Available Amount must be greater than or equal to 0' })
  @IsOptional()
  availableAmount?: number;

  @IsOptional()
  @IsString()
  @IsIn(['active', 'deactive'])
  status?: 'active' | 'deactive';

  @ValidateIf((o) => o.status === 'active')
  @IsNotEmpty({ message: 'Password is required when status is active' })
  @IsString()
  @IsOptional()
  password?: string;

  @ValidateIf((o) => o.status === 'active')
  @IsNotEmpty({ message: 'Smart OTP is required when status is active' })
  @IsString()
  @IsOptional()
  smartOTP?: string;

  @ValidateIf((o) => o.status === 'active')
  @IsNotEmpty({ message: 'Account No is required when status is active' })
  @IsString()
  @IsOptional()
  accountNo?: string;

  @ValidateIf((o) => o.status === 'active')
  @IsNotEmpty({ message: 'Account Name is required when status is active' })
  @IsString()
  @IsOptional()
  accountName?: string;
}

export interface DeviceConfig {
  deviceId: string;
  bankCode?: string;
  availableAmount: number;
  status: 'active' | 'deactive';
  password: string;
  smartOTP: string;
  accountNo: string;
  accountName: string;
}

