import { Controller, Post, Param, Get, Query, Logger, HttpException, HttpStatus, Inject } from '@nestjs/common';
import { ApiTags, ApiOperation, ApiParam, ApiQuery, ApiResponse } from '@nestjs/swagger';
import { BankService } from './bank.service';
import { HcBoxService } from '../hcbox/hcbox.service';
import { IBankService } from './interfaces/bank-service.interface';
import { config } from '@/common/constants/config';
import { IWebSocketService, PandaSwipeType } from '@/common/modules/websocket/interface/websocket.interface';
import { WEBSOCKET_SERVICE } from '@/common/modules/websocket/constants/websocket';

@ApiTags('bank')
@Controller('api/bank/:bankCode')
export class BankController {
  private readonly logger = new Logger('BankController');

  constructor(
    private readonly bankService: BankService,
    private readonly hcBoxService: HcBoxService,
    @Inject(WEBSOCKET_SERVICE)
    protected readonly wsService: IWebSocketService,
  ) {}

  /**
   * Get bank service by bank code
   */
  private getBankService(bankCode: string): IBankService {
    const service = this.bankService.getBankService(bankCode);
    if (!service) {
      throw new HttpException(
        {
          success: false,
          error: `Bank service not found for bank code: ${bankCode}. Supported banks: ${this.bankService.getSupportedBanks().join(', ')}`,
        },
        HttpStatus.BAD_REQUEST,
      );
    }
    return service;
  }

  @Get('devices')
  @ApiOperation({ summary: 'Get list of available devices' })
  @ApiParam({ name: 'bankCode', description: 'Bank code (ACB, PGBANK, etc.)' })
  @ApiResponse({ status: 200, description: 'List of devices retrieved successfully' })
  async getDevices(@Param('bankCode') bankCode: string) {
    try {
      const devices = await this.hcBoxService.listDevices();
      return {
        success: true,
        data: devices,
      };
    } catch (error) {
      this.logger.error(`Error getting devices: ${error.message}`);
      return {
        success: false,
        error: error.message,
      };
    }
  }

  @Post('test/launch-app/:deviceId')
  @ApiOperation({ summary: 'Test launching bank app on a device' })
  @ApiParam({ name: 'bankCode', description: 'Bank code (ACB, PGBANK, etc.)' })
  @ApiParam({ name: 'deviceId', description: 'Device ID' })
  @ApiResponse({ status: 200, description: 'App launched successfully' })
  async testLaunchApp(
    @Param('bankCode') bankCode: string,
    @Param('deviceId') deviceId: string,
  ) {
    try {
      const service = this.getBankService(bankCode);
      this.logger.log(`Testing ${bankCode} app launch on device: ${deviceId}`);
      
      await service.launchApp(deviceId);
      
      return {
        success: true,
        message: `${bankCode} app launched successfully on device ${deviceId}`,
      };
    } catch (error) {
      this.logger.error(`Error testing ${bankCode} app launch: ${error.message}`);
      return {
        success: false,
        error: error.message,
      };
    }
  }

  @Post('test/capture-screen/:deviceId')
  @ApiOperation({ summary: 'Test screen capture on a device' })
  @ApiParam({ name: 'bankCode', description: 'Bank code (ACB, PGBANK, etc.)' })
  @ApiParam({ name: 'deviceId', description: 'Device ID' })
  @ApiQuery({ name: 'name', description: 'Screen capture name (optional)', required: false })
  @ApiQuery({ name: 'quality', description: 'Image quality (0-1, default: 0.5)', required: false })
  @ApiResponse({ status: 200, description: 'Screen captured successfully' })
  async testCaptureScreen(
    @Param('bankCode') bankCode: string,
    @Param('deviceId') deviceId: string,
    @Query('name') name?: string,
    @Query('quality') quality?: string,
  ) {
    try {
      const service = this.getBankService(bankCode);
      this.logger.log(`Testing screen capture on device: ${deviceId}`);
      
      const qualityValue = quality ? parseFloat(quality) : 0.5;
      const captureName = name || `test-screen-capture-${Date.now()}`;
      
      const base64Image = await this.wsService.captureScreen(deviceId, {
        quality: qualityValue,
        folderPath: `C:/boxscreen/${captureName}.png`,
      });
      
      return {
        success: true,
        message: `Screen captured successfully on device ${deviceId}`,
        data: {
          base64Image,
          imageLength: base64Image?.length ?? 0,
        },
      };
    } catch (error) {
      this.logger.error(`Error testing screen capture: ${error.message}`);
      return {
        success: false,
        error: error.message,
      };
    }
  }

  @Post('test/click/:deviceId')
  @ApiOperation({ summary: 'Test clicking at specific coordinates on a device' })
  @ApiParam({ name: 'bankCode', description: 'Bank code (ACB, PGBANK, etc.)' })
  @ApiParam({ name: 'deviceId', description: 'Device ID' })
  @ApiQuery({ name: 'x', description: 'X coordinate (HC: 0-1, Panda: 0-100)', required: true })
  @ApiQuery({ name: 'y', description: 'Y coordinate (Hc: 0-1), Panda: 0-100', required: true })
  @ApiResponse({ status: 200, description: 'Click completed successfully' })
  async testClickAtCoordinates(
    @Param('bankCode') bankCode: string,
    @Param('deviceId') deviceId: string,
    @Query('x') x: string,
    @Query('y') y: string,
  ) {
    try {
      const xCoord = parseFloat(x);
      const yCoord = parseFloat(y);

      if (isNaN(xCoord) || isNaN(yCoord)) {
        return {
          success: false,
          error: 'Invalid coordinates. x and y must be numbers between 0 and 1',
          example: `/api/bank/${bankCode}/test/click/{deviceId}?x=0.5&y=0.55`,
        };
      }

      this.logger.log(`Testing click at coordinates (${xCoord}, ${yCoord}) on device: ${deviceId}`);
      
      await this.wsService.click(deviceId, xCoord, yCoord);
      
      return {
        success: true,
        message: `Click completed successfully at coordinates (${xCoord}, ${yCoord}) on device ${deviceId}`,
        coordinates: { x: xCoord, y: yCoord },
      };
    } catch (error) {
      this.logger.error(`Error testing click at coordinates: ${error.message}`);
      return {
        success: false,
        error: error.message,
      };
    }
  }

  @Post('test/swipe/:deviceId')
  @ApiOperation({ summary: 'Test swiping on a device. Format is determined by config.boxType (HC or PANDA)' })
  @ApiParam({ name: 'bankCode', description: 'Bank code (ACB, PGBANK, etc.)' })
  @ApiParam({ name: 'deviceId', description: 'Device ID' })
  @ApiQuery({ name: 'x', description: 'X coordinate (HC: 0-1, Panda: 0-100). Required for HC and Panda types 0,1,2,10. Optional for Panda types 4,5,6,7,8,9', required: false })
  @ApiQuery({ name: 'y', description: 'Y coordinate (HC: 0-1, Panda: 0-100). Required for HC and Panda types 0,1,2,10. Optional for Panda types 4,5,6,7,8,9', required: false })
  @ApiQuery({ name: 'type', description: 'Panda pointer event type: 0=(press), 1=(lift), 2=(move), 4=(scroll up), 5=(scroll down), 6=(swipe up), 7=(swipe down), 8=(swipe left), 9=(swipe right), 10=(click). Required for Panda format.', required: false })
  @ApiQuery({ name: 'endX', description: 'End X coordinate for HC (required for HC: 0-1)', required: false })
  @ApiQuery({ name: 'endY', description: 'End Y coordinate for HC (required for HC: 0-1)', required: false })
  @ApiQuery({ name: 'duration', description: 'Swipe duration for HC (default: 0.2)', required: false })
  @ApiResponse({ status: 200, description: 'Swipe completed successfully' })
  async testSwipe(
    @Param('bankCode') bankCode: string,
    @Param('deviceId') deviceId: string,
    @Query('x') x?: string,
    @Query('y') y?: string,
    @Query('type') type?: string,
    @Query('endX') endX?: string,
    @Query('endY') endY?: string,
    @Query('duration') duration?: string,
  ) {
    try {
      const service = this.getBankService(bankCode);
      
      // Determine format based on config.boxType
      const isPandaFormat = config.boxType === 'PANDA';
      const isHCFormat = config.boxType === 'HC';

      if (isPandaFormat) {
        // Panda format: type is required
        if (!type) {
          return {
            success: false,
            error: 'For Panda format: type is required',
            example: `/api/bank/${bankCode}/test/swipe/{deviceId}?type=0`,
          };
        }

        // Validate type: must be one of the valid Panda types
        const validTypes: PandaSwipeType[] = ['0', '1', '2', '4', '5', '6', '7', '8', '9', '10'];
        if (!validTypes.includes(type as PandaSwipeType)) {
          return {
            success: false,
            error: `Invalid type. Type must be one of: 0 (/press), 1 (/lift), 2 (/move), 4 (/scroll up), 5 (/scroll down), 6 (/swipe up), 7 (/swipe down), 8 (/swipe left), 9 (/swipe right), 10 (/click)`,
            example: `/api/bank/${bankCode}/test/swipe/{deviceId}?type=0`,
          };
        }

        // For scroll (4,5) and swipe directions (6,7,8,9), x and y are optional
        const noCoordinateTypes: PandaSwipeType[] = ['4', '5', '6', '7', '8', '9'];
        const isNoCoordinateType = noCoordinateTypes.includes(type as PandaSwipeType);

        // For other types (0,1,2,10), x and y are required
        if (!isNoCoordinateType && (!x || !y)) {
          return {
            success: false,
            error: `For Panda type ${type}: x and y are required`,
            example: `/api/bank/${bankCode}/test/swipe/{deviceId}?x=50&y=30&type=${type}`,
          };
        }

        // Build options object
        const pandaOptions: any = {
          type: type as PandaSwipeType,
        };

        // Only add x, y if provided (required for non-swipe-direction types, optional for swipe directions)
        if (x && y) {
          const xCoord = parseFloat(x);
          const yCoord = parseFloat(y);

          if (isNaN(xCoord) || isNaN(yCoord)) {
            return {
              success: false,
              error: 'Invalid coordinates. x and y must be numbers between 0 and 100 for Panda',
              example: `/api/bank/${bankCode}/test/swipe/{deviceId}?x=50&y=30&type=${type}`,
            };
          }

          // Validate coordinates for Panda (0-100)
          if (xCoord < 0 || xCoord > 100 || yCoord < 0 || yCoord > 100) {
            return {
              success: false,
              error: 'Invalid coordinates. x and y must be between 0 and 100 for Panda',
              example: `/api/bank/${bankCode}/test/swipe/{deviceId}?x=50&y=30&type=${type}`,
            };
          }

          pandaOptions.x = xCoord.toString();
          pandaOptions.y = yCoord.toString();
        }

        this.logger.log(`Testing Panda swipe on device: ${deviceId} with type=${type}${x && y ? `, x=${x}, y=${y}` : ' (swipe direction, no coordinates)'}`);
        
        await this.wsService.swipe(deviceId, pandaOptions);
        
        return {
          success: true,
          message: `Panda swipe completed successfully on device ${deviceId}`,
          coordinates: {
            type: type,
            ...(x && y ? { x, y } : {}),
          },
        };
      } else if (isHCFormat) {
        // HC format: x, y, endX, endY, duration (x, y, endX, endY required)
        if (!x || !y || !endX || !endY) {
          return {
            success: false,
            error: 'For HC format: x, y, endX, and endY are required',
            example: `/api/bank/${bankCode}/test/swipe/{deviceId}?x=0.2&y=0.5&endX=0.8&endY=0.5&duration=0.3`,
          };
        }

        const xCoord = parseFloat(x);
        const yCoord = parseFloat(y);
        const endXCoord = parseFloat(endX);
        const endYCoord = parseFloat(endY);
        const durationValue = duration ? parseFloat(duration) : 0.2;

        if (isNaN(xCoord) || isNaN(yCoord) || isNaN(endXCoord) || isNaN(endYCoord)) {
          return {
            success: false,
            error: 'Invalid coordinates. x, y, endX, and endY must be numbers between 0 and 1 for HC',
            example: `/api/bank/${bankCode}/test/swipe/{deviceId}?x=0.2&y=0.5&endX=0.8&endY=0.5&duration=0.3`,
          };
        }

        // Validate coordinates for HC (0-1)
        if (xCoord < 0 || xCoord > 1 || yCoord < 0 || yCoord > 1 || 
            endXCoord < 0 || endXCoord > 1 || endYCoord < 0 || endYCoord > 1) {
          return {
            success: false,
            error: 'Invalid coordinates. x, y, endX, and endY must be between 0 and 1 for HC',
            example: `/api/bank/${bankCode}/test/swipe/{deviceId}?x=0.2&y=0.5&endX=0.8&endY=0.5&duration=0.3`,
          };
        }

        this.logger.log(`Testing HC swipe from (${xCoord}, ${yCoord}) to (${endXCoord}, ${endYCoord}) on device: ${deviceId}`);
        
        await this.wsService.swipe(deviceId, {
          x: xCoord,
          y: yCoord,
          endX: endXCoord,
          endY: endYCoord,
          duration: durationValue,
        });
        
        return {
          success: true,
          message: `HC swipe completed successfully from (${xCoord}, ${yCoord}) to (${endXCoord}, ${endYCoord}) on device ${deviceId}`,
          coordinates: {
            start: { x: xCoord, y: yCoord },
            end: { x: endXCoord, y: endYCoord },
            duration: durationValue,
          },
        };
      } else {
        return {
          success: false,
          error: `Invalid box type configuration: ${config.boxType}. Must be 'HC' or 'PANDA'`,
        };
      }
    } catch (error) {
      this.logger.error(`Error testing swipe: ${error.message}`);
      return {
        success: false,
        error: error.message,
      };
    }
  }

  @Post('test/input-numpad/:deviceId')
  @ApiOperation({ summary: 'Test inputting number using numpad layout on a device' })
  @ApiParam({ name: 'bankCode', description: 'Bank code (ACB, PGBANK, etc.)' })
  @ApiParam({ name: 'deviceId', description: 'Device ID' })
  @ApiQuery({ name: 'number', description: 'Number string to input (e.g., "123", "456789")', required: true })
  @ApiResponse({ status: 200, description: 'Numpad number input completed successfully' })
  async testInputNumpadNumber(
    @Param('bankCode') bankCode: string,
    @Param('deviceId') deviceId: string,
    @Query('number') number: string,
  ) {
    try {
      const service = this.getBankService(bankCode);
      
      if (!number || number.trim().length === 0) {
        return {
          success: false,
          error: 'Number is required and cannot be empty',
          example: `/api/bank/${bankCode}/test/input-numpad/{deviceId}?number=123456`,
        };
      }

      // Validate that number contains only digits
      if (!/^\d+$/.test(number)) {
        return {
          success: false,
          error: 'Number must contain only digits (0-9)',
          example: `/api/bank/${bankCode}/test/input-numpad/{deviceId}?number=123456`,
        };
      }

      this.logger.log(`Testing numpad number input "${number}" on device: ${deviceId}`);
      
      await this.hcBoxService.inputNumpadNumber(deviceId, number);
      
      return {
        success: true,
        message: `Numpad number input completed successfully on device ${deviceId}`,
        data: {
          number: number,
          digits: number.split('').map(d => parseInt(d)),
        },
      };
    } catch (error) {
      this.logger.error(`Error testing numpad number input: ${error.message}`);
      return {
        success: false,
        error: error.message,
      };
    }
  }

  @Post('test/input-text/:deviceId')
  @ApiOperation({ summary: 'Test inputting text using keyboard layout on a device' })
  @ApiParam({ name: 'bankCode', description: 'Bank code (ACB, PGBANK, etc.)' })
  @ApiParam({ name: 'deviceId', description: 'Device ID' })
  @ApiQuery({ name: 'text', description: 'Text string to input (e.g., "abc123", "test@email.com")', required: true })
  @ApiResponse({ status: 200, description: 'Text input completed successfully' })
  async testInputText(
    @Param('bankCode') bankCode: string,
    @Param('deviceId') deviceId: string,
    @Query('text') text: string,
  ) {
    try {
      const service = this.getBankService(bankCode);
      
      if (!text || text.trim().length === 0) {
        return {
          success: false,
          error: 'Text is required and cannot be empty',
          example: `/api/bank/${bankCode}/test/input-text/{deviceId}?text=abc123`,
        };
      }

      this.logger.log(`Testing text input "${text}" on device: ${deviceId}`);
      
      await this.hcBoxService.inputText(deviceId, text);
      
      return {
        success: true,
        message: `Text input completed successfully on device ${deviceId}`,  
      };
    } catch (error) {
      this.logger.error(`Error testing text input: ${error.message}`);
      return {
        success: false,
        error: error.message,
      };
    }
  }

  @Post('test/press-return/:deviceId')
  @ApiOperation({ summary: 'Test pressing return key on a device' })
  @ApiParam({ name: 'bankCode', description: 'Bank code (ACB, PGBANK, etc.)' })
  @ApiParam({ name: 'deviceId', description: 'Device ID' })
  @ApiResponse({ status: 200, description: 'Return key pressed successfully' })
  async testPressReturn(
    @Param('bankCode') bankCode: string,
    @Param('deviceId') deviceId: string,
  ) {
    try {
      const service = this.getBankService(bankCode);
      this.logger.log(`Testing press return key on device: ${deviceId}`);
      
      await this.wsService.pressReturnKey(deviceId);
      
      return {
        success: true,
        message: `Return key pressed successfully on device ${deviceId}`,
        note: 'This key can dismiss keyboard or submit/enter depending on context',
      };
    } catch (error) {
      this.logger.error(`Error testing press return: ${error.message}`);
      return {
        success: false,
        error: error.message,
      };
    }
  }
}

