import { Controller, Get, Post, Logger } from '@nestjs/common';
import { ApiTags, ApiOperation, ApiResponse } from '@nestjs/swagger';
import { WithdrawalJobManagerService } from './withdrawal-job-manager.service';

@ApiTags('withdrawal')
@Controller('api/withdrawal')
export class WithdrawalController {
  private readonly logger = new Logger('WithdrawalController');

  constructor(
    private readonly jobManagerService: WithdrawalJobManagerService,
  ) {}

  @Get('queue/stats')
  @ApiOperation({ summary: 'Get withdrawal queue statistics' })
  @ApiResponse({ status: 200, description: 'Queue statistics' })
  async getQueueStats() {
    return await this.jobManagerService.getQueueStats();
  }

  @Get('queue/debug')
  @ApiOperation({ summary: 'Get detailed queue debug information' })
  @ApiResponse({ status: 200, description: 'Detailed queue debug info' })
  async debugQueue() {
    return await this.jobManagerService.debugQueue();
  }

  @Post('queue/trigger')
  @ApiOperation({ summary: 'Manually trigger job picking' })
  @ApiResponse({ status: 200, description: 'Job picking triggered' })
  async triggerJobPicking() {
    this.logger.log('Manually triggering job picking...');
    await this.jobManagerService.triggerPickJobs(1);
    const stats = await this.jobManagerService.getQueueStats();
    return {
      success: true,
      message: 'Job picking triggered',
      stats,
    };
  }

  @Post('queue/start')
  @ApiOperation({ summary: 'Manually start job processing' })
  @ApiResponse({ status: 200, description: 'Job processing started' })
  async startProcessing() {
    this.logger.log('Manually starting job processing...');
    await this.jobManagerService.startProcessing();
    const stats = await this.jobManagerService.getQueueStats();
    return {
      success: true,
      message: 'Job processing started',
      stats,
    };
  }
}


